<template>
  <div>
    <div class="search">
      <input v-model="keyword" type="text" class="search-input" placeholder="输入城市名或拼音">
    </div>
    <div class="search-content" ref="content" v-show="keyword">
      <ul>
        <li class="search-item border-bottom" v-for="item in list" :key="item.id" @click="handleCityClick(item.name)">{{item.name}}</li>
        <li class="search-item border-bottom" v-show="hasData">没有找到匹配的数据</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import {mapMutations} from 'vuex'
export default {
  name: "CitySearch",
  props: {
    citiesList: Object
  },
  data() {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  computed: {
    hasData() {
      return !this.list.length
    }
  },
  methods:{
    handleCityClick(city){
      this.changeCity(city)
      this.$router.push('/')      
    },
    ...mapMutations(['changeCity'])
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.citiesList) {
          this.citiesList[i].forEach(value => {
            if(value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          });
        }
        this.list = result
      }, 100);
    }
  },
  mounted() {
    this.scroll = new Bscroll(this.$refs.content)
  }
};
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl';
  .search
    height .72rem
    background $bgColor
    padding .1rem
    .search-input
      box-sizing border-box
      width 100%
      padding .1rem
      height .62rem
      line-height .62rem
      text-align center
      color #666
      border-radius .1rem
  .search-content
    z-index 1
    overflow hidden
    position absolute 
    top 1.8rem
    left 0
    bottom 0
    right 0
    background #eee
    .search-item
      line-height .62rem
      padding-left .2rem
      background-color #fff
      color #666
</style>



