<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img class="banner-img" :src="bannerImg" alt="">
      <div class="banner-info">
          <div class="banner-tittle">
            {{sightName}}
          </div>
          <div class="banner-number">
            <span class="iconfont banner-icon">&#xe63f;</span>
            {{bannerImgs.length}}
          </div>
        </div>
    </div>
    <common-gallary :imgs="bannerImgs" v-show="shwoGallary" @close="handleGallaryClose"></common-gallary>
  </div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
export default {
  name:'DetailBanner',
  props:{
    sightName: String,
    bannerImg: String,
    bannerImgs: Array
  },
  components: {
    CommonGallary
  },
  data() {
    return {
      shwoGallary: false
    }
  },
  methods: {
    handleBannerClick() {
      this.shwoGallary = true
    },
    handleGallaryClose() {
      this.shwoGallary = false
    }
  }
};
</script>

<style lang="stylus" scoped>
.banner
  position relative
  overflow hidden
  height 0
  padding-bottom: 55%
  .banner-img
    width 100%
  .banner-info
    display flex
    position absolute 
    left 0
    right 0
    bottom 0
    height .6rem
    line-height .6rem
    color #fff
    background-image linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,.8))
    .banner-tittle
      flex: 1
      font-size: .32rem
      padding: 0 .2rem
    .banner-number
      height: .32rem
      line-height: .32rem
      margin-top: .14rem
      padding: 0 .4rem
      border-radius: .2rem
      background: rgba(0, 0, 0, .8)
      font-size: .24rem
      .banner-icon
        font-size: .24rem

</style>



